import { Component, ViewChild } from '@angular/core';
import { ChildComponent1 } from './child1.component';

@Component({
    selector: 'app-parent1',
    // template: `
    //     <button (click)="child.value = child.value + 1">Add to child</button>
    //     <app-child1 #child></app-child1>
    // `
    template: `
        <button (click)="addForChild()">Add to child</button>
        <app-child1></app-child1>
    `
})

export class ParentComponent1{
    @ViewChild(ChildComponent1)
    myChild: ChildComponent1;
    addForChild(){
        this.myChild.value++;
    }
}
