import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GetWheatherService } from './getwheather.service';

@Component({
  selector: 'app-getwheather',
  templateUrl: './getwheather.component.html',
  styleUrls: ['./getwheather.component.css']
})
export class GetwheatherComponent implements OnInit {  
  nameCity = '';
  txtname = '';
  nhietdo: any;
  isLoading = false;
//https://samples.openweathermap.org/data/2.5/weather?q=London&appid=439d4b804bc8187953eb36d2a8c26a02
  constructor(private http: HttpClient, private _getWheatherService: GetWheatherService) { }

  ngOnInit(): void {
  }
  search(){
      this.isLoading = true;
      this._getWheatherService.getTemp(this.nameCity)
      .then(temp => 
        {
          this.txtname = this.nameCity;
          this.nhietdo = temp;
          this.isLoading = false;
        }
      )
      .catch(err => {
        alert('Không tìm thấy!!!');
        this.isLoading = false;
        this.nameCity = '';
        this.txtname = '';
      });
  }

  getMessage() {
    if(this.isLoading){
      return 'loading.......';
    }
    return this.txtname === '' ? 'Nhập địa điểm' : (this.txtname + ' nhiệt độ là ' + this.nhietdo);
  }
}
